// COLORS
:root {
  --cod-gray: #1a1a1a;
  --mine-shaft: #333333;
  --silver-chalice: #a0a0a0;
  --silver: #c4c4c4;
  --apple: #359c31;
  --apple-light: #4eb84a;
  --pastel-green: #6dd76a;
  --matrix: #ab5054;
  --white-100: #ffffff;
  --black-25: rgba(0, 0, 0, 0.25);

  --text: var(--cod-gray);
  --text-gray: var(--mine-shaft);
  --text-altern: var(--white-100);
  --text-notaccent: var(--silver-chalice);
  --bg: var(--white-100);
  --bg-altern: var(--cod-gray);
  --accent: var(--apple);

  --border: var(--silver);
  --checkbox: var(--silver-chalice);

  --focused: var(--apple-light);
  --actived: var(--pastel-green);
  --disabled: var(--silver);

  --warning: var(--matrix);
}
